import Layout from "../components/layout";
import Seo from "../components/seo";
import SmartContractScreen from "../screens/smart-contract";
import React from "react";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import BreadCrumb from "../components/common/breadcrumb";
import MoveForward from "../components/common/move-forward";


const SmartContract = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Smart Contract Development Services", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <SmartContractScreen db={db} />
      
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Smart Contract Development Services"
    headTitle="Smart Contract Development Company | Justtry Tech"
    description=" JustTry Tech is a smart contract development company that provides DeFi smart contract development services such as Binance smart contract development, Tron smart contract development, EOS smart contract development, and etc."
    keywords="Smart Contract Development, Solidity Smart Contracts, Ethereum Smart Contracts, Binance Smart Chain Development, Custom Smart Contract Solutions, Token Smart Contracts, DeFi Smart Contracts, NFT Smart Contracts, Smart Contract Auditing, Smart Contract Security, Smart Contract Optimization, Cross-Platform Smart Contracts, Smart Contract Integration, ERC-20 Token Development, ERC-721 Token Development, Tokenomics Services, Smart Contract Deployment, Smart Contract Consulting, Smart Contract Upgrades, Decentralized Applications (DApps), Blockchain Protocols, Multi-Signature Smart Contracts, Self-Executing Contracts, Oracle Integration for Smart Contracts, Smart Contract Development Company"
  ></Seo>
);

export default SmartContract;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
